import React, { Component } from "react";
import { connect } from "react-redux";
import { compose } from "redux";

import AssetFiscalCreateForm from "./createForm";

import notification from "../../../../components/notification";
// import { Input, Select, Divider } from "antd";

import LayoutWrapper from "../../../../components/utility/layoutWrapper";
import PageHeader from "../../../../components/utility/pageHeader";
import IntlMessages from "../../../../components/utility/intlMessages";
import Button from "../../../../components/uielements/button";
import actions from "../../../../redux/asset/assetFiscal/actions";
import { Box } from "../../../App/app.style";

const redirectTo = "/dashboard/setup/asset-fiscals"

class CreateAssetFiscal extends Component {
  // Get All data diawal
  componentDidMount() {
    const { createAssetFiscal } = this.props;
    createAssetFiscal();
  }

  componentDidUpdate() {
    // Display error notif
    if (this.props.assetFiscal.error) {
      notification("error", this.props.assetFiscal.error);
      this.props.clearError();
    }

    // Display success notif
    if (this.props.assetFiscal.success) {
      this.props.history.replace(redirectTo)
    }
  }

  handleSubmit = value => {
    const { storeAssetFiscal } = this.props;
    storeAssetFiscal(value);
  };

  render() {
    // console.log(this.props);

    return (
      <LayoutWrapper>
        <Box>
          <PageHeader title={<IntlMessages id="assetFiscals.add.title" />}
            style={{ margin: 0 }}
          >
            <Button onClick={() => this.props.history.replace(redirectTo)}>
              <IntlMessages id="button.back" />
            </Button>
          </PageHeader>

          <AssetFiscalCreateForm
            auth={this.props.auth}
            submit={this.handleSubmit}
            assetFiscal={this.props.assetFiscal}
          />
        </Box>
      </LayoutWrapper>
    );
  }
}

const mapStateToProps = state => {
  return {
    auth: state.auth,
    assetFiscal: state.assetFiscal
  };
};

export default compose(
  connect(
    mapStateToProps,
    actions
  )
)(CreateAssetFiscal);
